@import "../../../variable.scss";

div.WELCOME_CONTAINER {
    background-color: $DARK_BACKGROUND;
    background-image: linear-gradient(to bottom, #1b2b32c5 0%, #1b2b32c5 100%),
        url("./splash.jpg");
    background-position: center;
    background-size: cover;
    height: 100vh;

    div.row {
        height: 70%;

        div.Header {
            height: 200px !important;
            background-image: none;
            background-size: cover;
            background-position: center;
        }

        div.CONTAINER {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            height: 40%;

            h2 {
                color: white;
                font-family: Roboto;
                font-weight: lighter;
                font-stretch: normal;
                font-style: normal;
                letter-spacing: normal;
                text-align: center;
                margin: 0;
            }

            img {
                width: 500px;
                margin: 30px 0;
            }

            a {
                margin-top: 50px;
                width: 320px;
                text-align: center;
                color: white;
                font-family: Roboto;
                background-color: $PRIMARY_COLOR;
                padding: 10px 0px;
                border-radius: 3px;
                box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
                text-decoration: none !important;
                font-size: 12px;
            }
        }
    }
}

@media (max-width: 320px) {
    div.WELCOME_CONTAINER {
        background-image: linear-gradient(to bottom, #1b2b32c5 0%, #1b2b32c5 100%) !important;

        a {
            width: 250px !important;
        }

        div.row {
            div.Header {
                background-image: linear-gradient(to bottom, #1b2b32c5 0%, #1b2b32c5 100%),
                    url("./splash.jpg") !important;
            }
        }
    }
}